.App {
  display: flex;
  height: calc(100vh - 50px);
}
.navbar {
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 18px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  box-sizing: border-box;
}

.preview {
  width: 50vw;
  height: calc(100vh - 50px);
}
.preview-dashboard {
  height: 30px;
  background-color: #eee;
  display: flex;
  align-items: center;
  padding: 0 18px;
}
.preview iframe {
  box-sizing: border-box;
  outline: none;
  border: none;
  margin: 0;
  padding: 0;
  width: 50vw;
}

.preview-dashboard label {
  margin-right: 18px;
}

.preview-html #preview{
  display: none;
}
.preview-html iframe {
  height: calc(100vh - 85px);
}

.preview-both iframe {
  height: calc(50vh - 5px);
}

.preview-code iframe {
  display: none;
}